body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 200px;
}

.card {
  max-width: fit-content;
  border-radius: 12px;
  color: #5d382c;
  background-color: #fcecc9;
  padding: 24px;
  margin: 0 24px;
  box-shadow: 0 5px 15px rgba(158, 84, 0, 0.15);

  display: flex;
  justify-content: space-between;
}

h2,
h3 {
  margin-top: 0;
  margin-bottom: 8px;
}

.question {
  width: 65%;
}

.question p {
  font-size: 24px;
  margin: 0;
  max-width: 400px;
}

.options {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 12px;
  width: 30%;
}

.answer {
  border: 2px solid rgba(93, 56, 44, 0.3);
  background-color: inherit;
  border-radius: 4px;
  font-weight: 600;
  font-size: 16px;
  overflow: hidden;

  color: rgba(93, 56, 44, 0.9);
  padding: 4px 0;
  cursor: pointer;
  transition: all 0.3s;
}

.answer:hover {
  background-color: rgba(93, 56, 44, 0.7);
  color: #fcecc9;
}

.red {
  background-color: rgba(102, 7, 9, 0.9);
  color: #ffffff;
}

.yellow {
  background-color: rgba(245, 204, 92, 0.6);
}

.green {
  background-color: #21764e;
  color: #ffffff;
}

.score {
  box-shadow: 0 5px 15px rgba(83, 45, 0, 0.3);
  width: fit-content;
  font-size: 28px;
  border-radius: 12px;
  padding: 20px;

  display: flex;
  flex-direction: column;
  align-items: center;
}
